@keyframes error-breack {
    0% {
        transform: translate(0);
    }

    25% {
        transform: translate(10px);
    }

    50% {
        transform: translate(-10px);
    }

    75% {
        transform: translate(10px);
    }
    100% {
        transform: translate(-10px);
    }
}

.input {
    display: flex;
    flex-direction: column;
    position: relative;

    &--error {
        color: red;
        animation-name: error-breack;
        animation-duration: .3s;
        animation-iteration-count: 1;
    }

    &--valid {
        color: green;
    }

    &__name {
        padding-block-end: rem(5);
        padding-inline-start: rem(10);

    }

    &__field {
        padding: rem(12);
        border-radius: rem(5);
        border-color: $base-gray;
        border-width: rem(1);
        box-shadow: none;


        &--message {
            height: 200px;
            resize: none;

            @include tablet {
                height: 100%;
            }

        }

    }
}

.text-help {
    z-index: 2;
    background-color: $pink;
    padding: rem(10);
    border-radius: rem(5);
    color: $white;
    // justify-self: start;
    align-self: center;
    flex-shrink: 0;
    position: absolute;
    bottom: -53px;
    transform: translateX(-10px) translateX(10px) translateX(0);
    transition-property: transform;
    transition-duration: 300ms;

    &::before {
        position: absolute;
        top: -5px;
        transform: rotate(45deg);
        display: block;
        content: '';
        width: rem(10);
        height: rem(10);
        background-color: $pink;
    }
}